.customform-container {
  width: 100vw;
  height: 100vh;
  display: flex;
}
.customform-container .block {
  width: 50%;
  height: 100%;
  padding: 24px;
  overflow: auto;
}
.customform-container .create {
  border-right: 2px solid #c9c9c9;
}
.customform-container .create .ant-form-item-label {
  width: 96px;
}
.customform-container .create .anticon-close-circle {
  transition: color 0.3s;
  cursor: pointer;
}
.customform-container .create .anticon-close-circle:hover {
  color: #ff4d4f;
}
.customform-container .show {
  border-left: 2px solid #c9c9c9;
}
.customform-container .show .ant-form {
  max-width: 600px;
  margin: 0 auto;
}
.customform-container .show .ant-form .ant-form-item-label {
  width: 100px;
}
